      <img :src="Hotel[choice].image" class="imgS" :alt="'img' + (index + 1)" />
      <h4>{{ Hotel[choice].title }}</h4>
      <p>{{ Hotel[choice].content }}</p>
      <p>{{ Hotel[choice].price }}원</p>
      <button @click="modalOpen = false">닫기</button>
    </div>
