    <p>{{ hotel.price }}원</p>
    <button v-on:click="sinGoS(index)" type="button">허위매물신고</button>
    <span>{{ hotel.singo }}</span>
  </div>
</template>

<script>
import room from "./assets/js/room.js";
export default {
  name: "App",
  data() {
    return {
      choice: 0,
      Hotel: room,
      modalOpen: false,

      nav: ["호텔", "항공권", "렌터카"],
    };
  },
  components: {},
  methods: {
    sinGoS(i) {
      this.Hotel[i].singo += 1;
    },
  },
};
</script>
